










/**
 * 三级菜单
 */
import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import {traversingTree} from '@/util'

@Component
export default class ThirdMenuList extends Vue {

    @Prop({
        type: Array,
        default() {
            return []
        }
    })
    appRouterAuc: any

    get active() {
        let res = this.$route.matched[3]
        if (res) {
            return res.path
        }
        return ''
    }

    //当前第二级路由下的所有三级菜单
    get thirdMenu() {
        let res = this.$route.matched[2]
        let menuChildren: any = []
        if (res) {
            traversingTree(this.appRouterAuc, {
                childList: 'children',
                filterFun: (item: any) => {
                    if (item.path === res.path) {
                        menuChildren = item.children || []
                        return false
                    }
                }
            })
        }
        menuChildren = menuChildren.filter((item: any) => {
            if (item.meta) {
                return item.meta.isMenu && item.meta.isShow
            }
            return false
        })
        return menuChildren
    }

    @Watch('thirdMenu', {
        deep: true,
        immediate: true
    })
    watchThirdMenu() {
        //三级菜单存在时,自动跳转到第一个有权限的三级菜单
        if (this.$route.matched[2] && this.$route.path === this.$route.matched[2].path && this.thirdMenu.length) {
            this.$router.push(this.thirdMenu[0].path)
        }

    }

    choose(item: any) {
        if (item.path !== this.active) {
            this.$router.push(item.path)
        }
    }
}
